body{
	box-sizing: border-box;
	padding: 0px;
	margin: 0px;
	
}


header{
	display: flex;
	justify-content: center;
	padding: 14px;
	background: linear-gradient(0.25turn, #3f87a6, #ebf8e1, #f69d3c); 
}

header > img{
	height: 100px;
	width: 100px;
	padding-right: 30px;
}

.meme-form{
	display: flex;
	justify-content: center;
	width: 90%;
	margin:20px auto;
	

}
input[type="text"]{
	padding:10px;
	width: 45%;
}
.meme-form > button{
	border: none;
	font-family: VT323,monospace;
	font-size: 25px;
	color:white;

	background: linear-gradient(0.25turn, #3f87a6, #ebf8e1, #f69d3c);
}

.meme{
	position: relative;
	width: 90%;
	text-align: center;
	display: flex;
	justify-content: center;
	margin: auto;
}

.meme > img{
	width: 500px;
	height: 500px;
	object-fit: contain;
}

.meme > h2{
	position: absolute;
	display: flex;
	width: 300px;
	padding: 30px 50px;
	text-align: center;
	justify-content: center;
	align-content: center;
	align-self: center;;
	margin:30px 15px;
	align-items:center;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: center;
	 font-size: 1em;
    text-transform: uppercase;
      text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
	 word-wrap: normal;
	 overflow: hidden;
	height: auto;
	
	word-wrap: break-word;
	color: white;
	font-family: VT323,monospace;
	display: block;
}
.meme > .bottom{
	bottom: 0;
}
.meme > .top{
	top: 0;
}